import algoliasearch from 'algoliasearch/lite';
import React, { Component } from 'react';
import {
  InstantSearch,
  Hits,
  SearchBox,
  Pagination,
  Highlight,
  ClearRefinements,
  RefinementList,
  Configure,
} from 'react-instantsearch-dom';
import PropTypes from 'prop-types';
import './App.css';
import styled from "styled-components";


const searchClient = algoliasearch('XY58OU9HJ1', 'f43575d94f6c553fdfec03575b3b2239');

const LikeButton = styled.button`
  background-color: #f5a22e;
  color: white;
  font-size: 15px;
  float: left;
  padding: 10px;
  padding-right: 30px;
  padding-left: 30px;
  border-radius: 5px;
  margin: 10px 0px;
  cursor: pointer;
`;

const SaveButton = styled.button`
  background-color: #f5a22e;
  color: white;
  font-size: 15px;
  padding: 10px;
  border-radius: 5px;
  margin: 10px 0px;
  margin-right: 20px;
  cursor: pointer;
`;

class App extends Component  {
  render() {
    return (
      <div className="ais-InstantSearch">
        <h1>Create Your Very Own Vision Board<br /></h1>
        <InstantSearch indexName="vision_images" searchClient={searchClient}>
          <div className="left-panel">
            <ClearRefinements />
            <h2>category</h2>
            <div className="category-list">
            <RefinementList attribute="category" limit="40" />
            </div>
            <Configure hitsPerPage={40} />
          </div>
          <div className="right-panel">
            <SearchBox />
            <div className= "hits-wrapper">
            <Hits hitComponent={Hit} />
            </div>
            <Pagination />
          </div>
        </InstantSearch>
      </div>
    );
  }
}

function Hit(props) {
  return (
    <div className="props-container">
      <div className="hit-photo">
      <img src={props.hit.image} align="left" alt="" />
      </div>
      <div className="hit-description">
        <Highlight attribute="description" hit={props.hit} /></div>
      <div className="hit-photographer">Photographer: {props.hit.photographer}</div>
      <div className="hit-likes">Likes: {props.hit.likes}</div>
      <div className="hit-saves">Saves: {props.hit.saves}</div>
      <LikeButton>Like</LikeButton>
      <div class="divider"/>
      <SaveButton>Save to Board</SaveButton>
    </div>
  );
}


Hit.propTypes = {
  hit: PropTypes.object.isRequired,
};

export default App;
